<template>
  <div class="con">
    <publicTop></publicTop>
    <div class="home-top">
      <div class="home-top-l" @click="goB">
        <img src="@/assets/left-le.png" />
      </div>
      <div class="home-top-r">
        <!-- <img
          src="@/assets/icon_collect.png"
          class="home-top-r-img1"
          v-if="!contentDetail.flag"
          @click="goingCollect"
        /> -->
        <!-- <img
          src="@/assets/icon_collected.png"
          class="home-top-r-img1"
          @click="goingCollect"
        /> -->

        <!-- <img
          src="@/assets/icon_share.png"
          class="home-top-r-img2"
          @click="goShare"
        /> -->
        详情
      </div>
    </div>

    <!-- 头部 -->
    <div
      class="home-head"
      :style="`background-image: url(${url});background-size: 100% 100%;`"
    >
      <div class="home-head-name">{{ contentDetail.activity.title }}</div>
      <div class="home-head-ti">
        发布者：{{ contentDetail.activity.createBy.name }}
        <span>参加人数：{{ contentDetail.activity.joinNumber }}</span>
      </div>

      <div class="home-kuang">
        <div class="home-kuang-text">
          报名时间：{{ contentDetail.activity.regStartTime }}-{{
            contentDetail.activity.regEndTime
          }}
        </div>
        <div class="home-kuang-text">
          举办时间：{{ contentDetail.activity.startTime }}-{{
            contentDetail.activity.endTime
          }}
        </div>
        <div class="home-kuang-text">
          人数规模：{{ contentDetail.activity.scale }}
        </div>
        <div class="home-kuang-text">
          举办地点：{{ contentDetail.activity.address }}
        </div>
      </div>
    </div>

    <!-- 中部 -->
    <div class="head-body">
      <div class="head-body-top">活动具体内容</div>
      <div
        class="head-body-content"
        v-html="contentDetail.activity.content"
      ></div>
    </div>
    <div class="fixed-foot">
      <img
        src="@/assets/btn-read.png"
        class="fixed-foot-l"
        v-if="contentDetail.subFlag == false"
        @click="toSub"
      />
      <img
        src="@/assets/btn-readed.png"
        class="fixed-foot-l"
        v-if="contentDetail.subFlag == true"
        @click="toSubCancle"
      />
      <img
        src="@/assets/btn-join.png"
        class="fixed-foot-r"
        v-if="contentDetail.enterFlag == false"
        @click="toBao"
      />
      <img
        src="@/assets/btn-joined.png"
        class="fixed-foot-r"
        v-if="contentDetail.enterFlag == true"
        @click="toBaoCan"
      />
    </div>

    <!-- 分享弹框 -->
    <div class="add-jian" v-if="isShare == true" @click="goCloseSha">
      <div class="add-jian-share" @click.stop="gS">
        <div class="add-jian-share-t">
          <div
            v-for="(item, index) in shareList"
            :key="index"
            class="add-jian-share-t-item"
            @click="toShar(item)"
          >
            <img :src="item.img" alt="" />
            <div class="add-jian-share-t-item-text">{{ item.name }}</div>
          </div>
        </div>
        <div class="add-jian-share-end" @click="goCloseSha">取消</div>
      </div>
    </div>
    <!-- 报名弹框 -->
    <div class="add-jian" v-if="isSecond == true" @click="goClose2">
      <div class="add-jian-div" @click.stop="gu2">
        <div class="add-jian-div-head">
          <div class="add-jian-div-head-l">活动信息</div>
          <img src="@/assets/guan-close.png" @click="goClose2" />
        </div>
        <div class="bao-div">
          <span>姓名：</span>{{ studentDetail.studentName }}
        </div>
        <div class="bao-div">
          <span>学院：</span>{{ studentDetail.colleageName }}
        </div>
        <div class="bao-div">
          <span>学号：</span>{{ studentDetail.studentId }}
        </div>
        <div class="bao-div">
          <span>专业：</span>{{ studentDetail.mojorName }}
        </div>
        <div class="bao-div">
          <span>性别：</span>{{ studentDetail.sex == 1 ? "男" : "女" }}
        </div>
        <div class="bao-div">
          <span>手机号：</span>
          <input type="text" v-model="phoneNumber" placeholder="请输入手机号" />
        </div>
        <div class="bao-div">
          <span>微信号：</span>
          <input type="text" v-model="chatNumber" placeholder="请输入微信号" />
        </div>
        <div class="bao-div" v-if="jianZhi.length != 0">
          <span>请选择时间段：</span>
        </div>
        <div class="add-jian-div-neck" v-if="jianZhi.length != 0">
          <div
            class="add-jian-div-neck-item"
            v-for="(item, index) in jianZhi"
            :key="index"
          >
            <img
              src="@/assets/cell-select.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == false"
              @click="toSelect1(index)"
            />
            <img
              src="@/assets/cell-selected.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == true"
              @click="toSelect1(index)"
            />
            <div class="add-jian-div-neck-item-r">
              <div class="add-jian-div-neck-item-r-mid">
                <div class="add-jian-div-neck-item-r-mid-t">
                  {{ item.regStartTime }}---{{ item.regEndTime }}

                  {{
                    item.flag == 1
                      ? "(报名未开始)"
                      : item.flag == 2
                      ? "(报名已结束)"
                      : item.flag == 3
                      ? "(报名已满)"
                      : ""
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="add-jian-div-foot">
          <img
            src="@/assets/btn-longB.png"
            class="add-jian-div-foot-r"
            @click="goBM"
          />
        </div>
      </div>
    </div>
    <!-- 订阅弹框 -->
    <div
      class="add-jian"
      v-if="isSub == true && jianData.length != 0"
      @click="goClose3"
    >
      <div class="add-jian-div" @click.stop="gu3">
        <div class="add-jian-div-head">
          <div class="add-jian-div-head-l">请选择时间段</div>
          <img src="@/assets/guan-close.png" @click="goClose3" />
        </div>

        <div class="add-jian-div-neck">
          <div
            class="add-jian-div-neck-item"
            v-for="(item, index) in jianData"
            :key="index"
          >
            <img
              src="@/assets/cell-select.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == false"
              @click="toSelect3(index)"
            />
            <img
              src="@/assets/cell-selected.png"
              class="add-jian-div-neck-item-l"
              v-if="item.selected1 == true"
              @click="toSelect3(index)"
            />
            <div class="add-jian-div-neck-item-r">
              <div class="add-jian-div-neck-item-r-mid">
                <div class="add-jian-div-neck-item-r-mid-t">
                  {{ item.startTime }}---{{ item.endTime }}

                  {{
                    item.subFlag == 1
                      ? "(未开始)"
                      : item.subFlag == 2
                      ? "(已结束)"
                      : ""
                  }}
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="add-jian-div-foot">
          <img
            src="@/assets/btn-longD.png"
            class="add-jian-div-foot-r"
            @click="goSubscribe"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "activityDetail",
  components: {
    publicTop,
  },
  data() {
    return {
      url: require("../assets/bgB.png"),
      dec: "覆盖全国卓越",
      contentDetail: {},
      xuanId: "",
      isShare: false,
      shareList: [
        {
          img: require("../assets/wechat.png"),
          name: "微信",
          nativeshare: "wechatFriend",
          m_share: "wx",
        },
        {
          img: require("../assets/quan.png"),
          name: "朋友圈",
          nativeshare: "wechatTimeline",
          m_share: "wxline",
        },
        {
          img: require("../assets/qq.png"),
          name: "QQ",
          nativeshare: "qqFriend",
          m_share: "qq",
        },
        {
          img: require("../assets/weibo.png"),
          name: "微博",
          nativeshare: "weibo",
          m_share: "sina",
        },
      ],
      bgc: this.GLOBAL.bgc,
      jianZhi: [],
      jianData: [],
      isSecond: false,
      chatNumber: "",
      phoneNumber: "",
      isSub: false,
      flag: 9,
      studentDetail: {},
      specificTimeId: "",
      specificTimeId1: "",
    };
  },
  methods: {
    toShar(item) {
      if (item.name == "微博") {
        var url =
          "http://v.t.sina.com.cn/share/share.php" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("活动在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
      } else if (item.name == "QQ") {
        var url =
          "https://connect.qq.com/widget/shareqq/index.html" +
          "?url=" +
          encodeURIComponent(window.location.href) +
          "&title=" +
          encodeURIComponent("活动在即，欢迎咨询，万分感谢！") +
          "&source=" +
          encodeURIComponent(
            window.location.href.substring(0, window.location.href.indexOf("?"))
          ) +
          "&summary=" +
          encodeURIComponent(document.title) +
          "&pics=" +
          encodeURIComponent(
            "https://wechat.cdzjkl.com/files/options/avatar1584946687810.jpg"
          ) +
          "&desc=" +
          encodeURIComponent("");
        var a = document.createElement("a");
        a.href = url;
        a.target = "_blank";
        a.click();
      } else {
        console.log(item);
      }
    },
    // 收藏企业
    goingCollect() {
      const params = {
        recruitmentinfo: this.xuanId,
      };
      this.$axios
        // .get("/a/collectioninfo/saveCollection?recruitmentinfo=" + this.xuanId)
        .post("/a/collectioninfo/saveCollection", qs.stringify(params))
        .then((res) => {
          console.log(res, "收藏");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            this.getDetailContent();
          } else if (res.data.state == 4) {
            this.$toast.success(res.data.msg);
            this.getDetailContent();
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            // 判断去登录
            window.location.href = res.data.data;
            this.getDetailContent();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goClose2() {
      this.isSecond = false;
      for (var i = 0; i < this.jianZhi.length; i++) {
        this.jianZhi[i].selected1 = false;
      }
    },
    gu2() {},
    toSelect1(index) {
      for (var i = 0; i < this.jianZhi.length; i++) {
        if (i == index) {
          if (this.jianZhi[index].flag == 1) {
            this.jianZhi[index].selected1 = false;
            this.$toast("报名未开始");
          } else if (this.jianZhi[index].flag == 2) {
            this.jianZhi[index].selected1 = false;
            this.$toast("报名已结束");
          } else if (this.jianZhi[index].flag == 3) {
            this.jianZhi[index].selected1 = false;
            this.$toast("报名已满");
          } else {
            if (this.jianZhi[i].selected1 == false) {
              this.jianZhi[index].selected1 = true;
            } else {
              this.jianZhi[index].selected1 = false;
            }
          }
        } else {
          this.jianZhi[i].selected1 = false;
        }
      }
    },
    goClose3() {
      this.isSub = false;
      for (var i = 0; i < this.jianData.length; i++) {
        this.jianData[i].selected1 = false;
      }
    },
    gu3() {},
    toSelect3(index) {
      for (var i = 0; i < this.jianData.length; i++) {
        if (i == index) {
          if (this.jianData[index].subFlag == 1) {
            this.jianData[index].selected1 = false;
            this.$toast("未开始");
          } else if (this.jianData[index].subFlag == 2) {
            this.jianData[index].selected1 = false;
            this.$toast("已结束");
          } else {
            if (this.jianData[i].selected1 == false) {
              this.jianData[index].selected1 = true;
            } else {
              this.jianData[index].selected1 = false;
            }
          }
        }
      }
    },
    // 订阅
    toSub() {
      // 判断是否需要完善个人信息
      this.$axios
        .post("/f/common/activity/checkNeedPerfectInfo")
        .then((res) => {
          console.log(
            res,
            "是否需要完善个人信息",
            this.contentDetail.activity.specificTimeList
          );
          if (res.data.state == 1) {
            if (
              this.contentDetail.activity.specificTimeList &&
              this.contentDetail.activity.specificTimeList.length != 0
            ) {
              this.flag = 2;
              this.getStudentContent();
            } else {
              // 调订阅的接口
              this.goSubscribe();
            }
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            window.location.href = res.data.data;
            this.getDetailContent();
          } else if (res.data.state == 0) {
            this.$toast(res.data.msg);
            window.location.href = res.data.data;
            this.getDetailContent();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goSubscribe() {
      if (this.jianData.length != 0) {
        var lo1 = this.jianData.every((item) => {
          return item.subFlag == 2;
        });
        var lo2 = this.jianData.every((item) => {
          return item.subFlag == 1;
        });
        var lo3 = this.jianData.every((item) => {
          return item.selected1 == false;
        });

        var lo5 = this.jianData.some((item) => {
          return item.subFlag == 0;
        });
        // if (lo1 == true) {
        //   this.specificTimeId = "";
        // } else if (lo2 == true) {
        //   this.specificTimeId = "";
        // } else if (lo3 == true) {
        //   this.$toast("请选择时间段");
        //   return;
        // }

        if (lo5 == true && lo3 == true) {
          this.$toast("请选择时间段");
          return;
        } else {
          this.specificTimeId = "";
        }
        var ids = [];
        for (var i = 0; i < this.jianData.length; i++) {
          if (this.jianData[i].selected1) {
            ids.push(this.jianData[i].id);
          }
          // this.jianData[i].selected1 = false;
        }
        this.specificTimeId = ids.join(",");
        console.log(this.specificTimeId, "ll");
        const params1 = {
          id: this.xuanId,
          specificTimeIds: this.specificTimeId,
        };
        this.$axios
          // .get(
          //   "/f/common/activity/ajax_subscribe?id=" +
          //     this.xuanId +
          //     "&specificTimeIds=" +
          //     this.specificTimeId
          // )
          .post("/f/common/activity/ajax_subscribe", qs.stringify(params1))
          .then((res) => {
            console.log(res, "订阅");
            if (res.data.state == 1) {
              this.$toast.success(res.data.msg);
              this.getDetailContent();
              this.isSub = false;
              for (var i = 0; i < this.jianData.length; i++) {
                this.jianData[i].selected1 = false;
              }
            } else if (res.data.state == 2) {
              this.$toast(res.data.msg);
              window.location.href = res.data.data;
              this.getDetailContent();
              this.isSub = false;
            } else if (res.data.state == 0) {
              this.$toast(res.data.msg);
              this.getDetailContent();
            } else {
              this.$toast(res.data.msg);
            }
          })
          .catch((err) => {
            console.log(err, "获取失败");
          });
      } else {
        const params2 = {
          id: this.xuanId,
          specificTimeIds: this.specificTimeId,
        };
        this.$axios
          // .get(
          //   "/f/common/activity/ajax_subscribe?id=" +
          //     this.xuanId +
          //     "&specificTimeIds=" +
          //     this.specificTimeId
          // )
          .post("/f/common/activity/ajax_subscribe", qs.stringify(params2))
          .then((res) => {
            console.log(res, "订阅");
            if (res.data.state == 1) {
              this.$toast.success(res.data.msg);
              this.getDetailContent();
              this.isSub = false;
            } else if (res.data.state == 2) {
              this.$toast(res.data.msg);
              window.location.href = res.data.data;
              this.getDetailContent();
              this.isSub = false;
            } else if (res.data.state == 0) {
              this.$toast(res.data.msg);
              this.getDetailContent();
            } else {
              this.$toast(res.data.msg);
            }
          })
          .catch((err) => {
            console.log(err, "获取失败");
          });
      }
    },
    toSubCancle() {
      const params3 = {
        id: this.xuanId,
      };
      this.$axios
        // .get("/f/common/activity/ajax_cancelSubscribe?id=" + this.xuanId)
        .post("/f/common/activity/ajax_cancelSubscribe", qs.stringify(params3))
        .then((res) => {
          console.log(res, "取消订阅");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            this.getDetailContent();
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            window.location.href = res.data.data;
            this.getDetailContent();
          } else if (res.data.state == 0) {
            this.$toast(res.data.msg);
            this.getDetailContent();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    toBaoCan() {
      const params4 = {
        id: this.xuanId,
      };
      this.$axios
        // .get("/f/common/activity/ajax_cancelEntered?id=" + this.xuanId)
        .post("/f/common/activity/ajax_cancelEntered", qs.stringify(params4))
        .then((res) => {
          console.log(res, "取消报名");
          if (res.data.state == 1) {
            this.$toast.success(res.data.msg);
            this.getDetailContent();
          } else if (res.data.state == 2) {
            this.$toast(res.data.msg);
            window.location.href = res.data.data;
            this.getDetailContent();
          } else if (res.data.state == 0) {
            this.$toast(res.data.msg);
            this.getDetailContent();
          } else {
            this.$toast(res.data.msg);
          }
        })
        .catch((err) => {
          console.log(err, "获取失败");
        });
    },
    goBM() {
      if (!this.phoneNumber) {
        this.$toast("请填写手机号");
        return;
      }
      if (!this.chatNumber) {
        this.$toast("请填写微信号");
        return;
      }
      if (this.jianZhi.length != 0) {
        var lo1 = this.jianZhi.every((item) => {
          return item.flag == 2;
        });
        var lo2 = this.jianZhi.every((item) => {
          return item.flag == 1;
        });
        var lo4 = this.jianZhi.every((item) => {
          return item.flag == 3;
        });
        var lo3 = this.jianZhi.every((item) => {
          return item.selected1 == false;
        });

        var lo5 = this.jianZhi.some((item) => {
          return item.flag == 0;
        });
        // if (lo1 == true) {
        //   this.specificTimeId1 = "";
        // } else if (lo2 == true) {
        //   this.specificTimeId1 = "";
        // } else if (lo4 == true) {
        //   this.specificTimeId1 = "";
        // } else if (lo3 == true) {
        //   this.$toast("请选择时间段");
        //   return;
        // }

        if (lo5 == true && lo3 == true) {
          this.$toast("请选择时间段");
          return;
        } else {
          this.specificTimeId1 = "";
        }

        for (var i = 0; i < this.jianZhi.length; i++) {
          if (this.jianZhi[i].selected1) {
            this.specificTimeId1 = this.jianZhi[i].id;
          }
          // this.jianZhi[i].selected1 = false;
        }
        console.log(this.specificTimeId1, "lll");
        const params5 = {
          id: this.xuanId,
          specificTimeId: this.specificTimeId1,
          studentId: this.studentDetail.studentId,
          phoneNumber: this.phoneNumber,
          weChat: this.chatNumber,
        };
        this.$axios
          // .get(
          //   "/f/common/activity/ajax_entered?id=" +
          //     this.xuanId +
          //     "&specificTimeId=" +
          //     this.specificTimeId1 +
          //     "&studentId=" +
          //     this.studentDetail.studentId +
          //     "&phoneNumber=" +
          //     this.phoneNumber +
          //     "&weChat=" +
          //     this.chatNumber
          // )
          .post("/f/common/activity/ajax_entered", qs.stringify(params5))
          .then((res) => {
            console.log(res, "报名");
            if (res.data.state == 1) {
              this.$toast.success(res.data.msg);
              this.getDetailContent();
              this.isSecond = false;
              for (var i = 0; i < this.jianZhi.length; i++) {
                this.jianZhi[i].selected1 = false;
              }
            } else if (res.data.state == 2) {
              this.$toast(res.data.msg);
              window.location.href = res.data.data;
              this.getDetailContent();
              this.isSecond = false;
            } else if (res.data.state == 0) {
              this.$toast(res.data.msg);
              this.getDetailContent();
            } else {
              this.$toast(res.data.msg);
            }
